import { userCol } from "./firestore";
import { doc, setDoc } from "firebase/firestore";

async function createUser(data) {
  /* 새로운 유저 등록 */

  const response = {
    ok: false,
    message: null
  }

  /* 데이터 검사 */
  if (!data) {
    // 인자가 undefined나 null일 경우 중지
    response.message = "입력된 유저 정보가 없습니다.";
    return response;
  } else {
    // 각각의 요소들이 빈 칸이거나 undefined, null일 경우 중지
    const { id, password, question, answer } = data;
    if (!(id && password && question && answer)) {
      response.message = "빈 칸이거나, 잘못된 입력값이 있습니다."
      return response;
    }
  }

  /* 실행 */
  try {
    const userRef = doc(userCol); // 자동 생성된 id를 참조하기 위함

    await setDoc(userRef, {
      ...data,
      survey: {},
      uid: userRef.id // uid를 유저 정보와 같은 레벨에 있도록 함
    })

    response.ok = true;
    response.user = {
      ...data,
      uid: userRef.id
    }
    return response;

  } catch (error) {
    console.log(error);
    response.message = error.message;

    return response;
  }
}

export default createUser;