import { db } from "./firestore";
import { doc, updateDoc } from "firebase/firestore";

async function setProfile(uid, data) {
  /* 프로필 설정 */

  const response = {
    ok: false,
    message: null
  }

  /* 데이터 양식 일람
     이 범위를 벗어나면 통계자료에 오류가 생길 수 있음 */
  const genderList = ["남자", "여자"];
  const ageList = ["10대", "20대", "30대", "40대", "청춘"];
  const MBTIList = ["ESTJ", "ESTP", "ESFJ", "ESFP", "ENTJ", "ENTP", "ENFJ", "ENFP", "INFP", "INFJ", "INTP", "INTJ", "ISFP", "ISFJ", "ISTP", "ISTJ"];


  /* 데이터 검사 */
  if (!(uid && data)) {
    response.message = "로그인 정보가 없거나, 변경할 프로필 정보가 없습니다.";
    return response;
  }

  const { gender, age, MBTI, profileImage } = data;
  if (!(genderList.includes(gender) && ageList.includes(age) && MBTIList.includes(MBTI))) {
    response.message = "성별, 나이, MBTI에 범위를 벗어난 정보가 포함돼있습니다."
    return response;
  }


  try {
    const userRef = doc(db, "Users", uid); // 자동 생성된 id를 참조하기 위함

    await updateDoc(userRef, {
      profile: data
    })

    response.profile = data;
    response.ok = true;

    return response;

  } catch (error) {
    console.log(error);
    response.message = error.message;

    return response;
  }
}

export default setProfile;