import { surveyCol } from "./firestore";
import { getDocs, orderBy, query, where } from "firebase/firestore";

async function getSurveyList(start = 0, end = 3) {
  /* 서베이 리스트를 구간별로 가져온다
     현재로서는 구간별로 가져올 필요가 없어서 전체 가져오기 */

  const surveys = [];
  const q = query(surveyCol, orderBy("surveyNum")); // surveyNum을 기준으로 오름차순 정렬하는 쿼리

  const res = await getDocs(q);
  res.forEach(doc => {
    surveys.push(doc.data());
  })

  return surveys;
}

export default getSurveyList;