import { userCol } from "./firestore";
import { query, where, getDocs } from "firebase/firestore";

async function checkId(id) {
  /* db에 존재하는 아이디인지 확인 */

  const q = query(userCol, where("id", "==", id)); // 콜렉션에 동일한 아이디를 찾는 쿼리
  const response = await getDocs(q); // 요청 보내고 응답 받음

  let comparison;
  response.forEach((doc) => {
    comparison = (doc.data());
  })

  // 존재하는 경우 해당 id 문서, 존재하지 않는 경우 undefined return
  return comparison;
}

export default checkId;