import checkId from "./checkId";

async function login(id, password) {
  /* 로그인 실행
     아이디와 비밀번호 일치 시 유저 정보를 return */

  const response = {
    ok: false,
    message: null,
  }

  try {
    const res = await checkId(id);

    if (!res) { // 아이디가 존재하지 않는 경우
      response.message = "존재하지 않는 ID입니다.";
    }
    else if (res.password !== password) { // 해당 아이디의 비밀번호가 일치하지 않는 경우
      response.message = "비밀번호가 틀립니다.";
    }
    else if (res.password === password) { // 아이디와 비밀번호가 일치할 경우 유저 정보를 응답에 포함
      response.ok = true;
      response.user = res;
    }

    return response;

  } catch (error) {
    console.log(error);
    response.message = error.message;

    return response;
  }
}

export default login;